body{
   
    margin: 5%;
    background-color: #f3f3f3;
}
input, .container2,.options input,.div,h1, .choice select
{
border-radius: 0.5em;
width:100%;
height: 50%;
}
.trash, .add
{
    background-color: white;
    color:black;
    border-radius: 1em;
    padding:0.5em;
    border: 1px solid #efefef;
    margin: 2% 1%;
}
input,.choice select{
    height: 60%;
    margin: 0.5em 0;
    border: 1px solid #efefef;
}
.qs
{
    flex:1;
    padding: 1% 0;
}
.qs::placeholder
{
color:black;
font-weight: bold;
}

        .container2
        {
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
        }
        .choice{
            flex:0.5;
        }
        .options
        {
            margin: 0 1em;
            flex:2.2;
        }
        .options input{
            margin:0 0 0.5em 0;
        }
        a{
            color:black;
        }
       .div
        {
            border: 1px solid #efefef;
            padding: 1em 0.6rem 3.5rem 0.6rem;
            background-color: white;
            margin: 1% 0;
        }
        h1
        {
            border: 1px solid #efefef;
        }
        
        @media screen and (max-width:865px)
        {
            body {
            margin:3% 0;
            }
        }


        @media screen and (max-width:650px)
        {
            body {
            margin:2% 0;
            }
        }

        @media screen and (max-width:500px)
        {
            body {
            margin:2% 0;
            }
            .container2
            {
                flex-direction: column;
            }
            input,button,.choice select,.options{
                padding:3%;
                margin:7% 0;
            }
            .qs
            {
                padding: 7% 0;
            }
            .options{
                margin-left: 1em;
                margin-right: 1em;
                flex: 1.6;
            }
        }